import { AnimatePresence, motion } from 'framer-motion'

import chipTrayChips1 from '@repo/ui/assets/chipTray/chips1.webp'
import chipTrayChips2 from '@repo/ui/assets/chipTray/chips2.webp'
import logoLightImg from '@repo/ui/assets/chipTray/logoLight.webp'
import Semicircle from '@repo/ui/assets/chipTray/Semicircle.webp'
import ChipActive from '@repo/ui/components/chipTray/ChipActive'
import Image from '@repo/ui/components/common/Image'

import { useCartStore } from '@repo/ui/store/useCartStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { isChipActive, isChipDisabled } from '@repo/ui/store/utils/chips'
import { cn } from '@repo/utils/common/common'
import useChipTrayPress from '@repo/utils/hook/useChipTrayPress'

import ChipTrayAnimation from '../animatedContainer/ChipTrayAnimation'
import FooterAnimation from '../animatedContainer/FooterAnimation'
import ClickableArea from '../common/clickableArea/ClickableArea'
import FooterChip from '../layout/footer/mobile/FooterChip'
import Chip from './Chip'
import './ChipTray.scss'
import ShineBorder from './ShineBorder'

const chipsVariants = {
  closed: {
    opacity: 0,
    x: 0,
    y: 0,
  },
  open: (index: number) => {
    const totalItems = 8
    const angleRange = (166 * Math.PI) / 180
    const startAngle = -Math.PI + ((14 / 2) * Math.PI) / 180
    const angle = startAngle + (index * angleRange) / (totalItems - 1)
    const radius = 8.8
    return {
      opacity: 1,
      x: Math.cos(angle) * radius * 0.95 + 'rem',
      y: Math.sin(angle) * radius + 'rem',
    }
  },
}

type ChipTrayProps = {
  enableAnimation?: boolean
}

export default function ChipTray({ enableAnimation = false }: ChipTrayProps) {
  const isOpen = useLayoutStore((s) => s.isOpenSelector)
  const toggleSelector = useLayoutStore((s) => s.toggleSelector)
  const chipTrayList = useCartStore((s) => s.chipList).filter((chip) => chip.onTable === true)
  const selectedChipVal = useCartStore((s) => s.selectedChipVal)
  const selectedChipIdx = useCartStore((s) => s.selectedChipIdx)
  const isSquare = selectedChipIdx === 11

  const pressChipValue = useChipTrayPress()

  return (
    <div
      id="ChipTray"
      className={cn('contents', { isOpen })}
    >
      <div className="blackBackground"></div>

      {isOpen && (
        <div
          className="mask"
          onClick={() => toggleSelector()}
        ></div>
      )}

      <div className={cn('center absolute bottom-0 z-[1] w-full text-[1.25rem]', !isOpen && 'pointer-events-none')}>
        <div className="chipWrap z-10">
          <img
            src={logoLightImg}
            className="SvgLogo"
            alt="Crystal Logo Light"
          />
          <AnimatePresence>
            {chipTrayList.map((chip, idx) => {
              const isActive = isChipActive(chip.amount)
              const isDisabled = isChipDisabled(chip.amount)
              return (
                <motion.div
                  initial="closed"
                  animate={isOpen ? 'open' : 'closed'}
                  key={idx}
                  variants={chipsVariants}
                  custom={idx}
                  transition={{
                    duration: 0.5,
                    type: 'spring',
                    stiffness: 256,
                    damping: 24,
                    mass: 1,
                  }}
                >
                  <Chip
                    value={chip.amount}
                    isActive={isActive}
                    isDisabled={isDisabled}
                    pressChipValue={pressChipValue}
                    className="chipTrayChip"
                  />
                  {/* fading effect */}
                  {chip.amount === selectedChipVal && <ChipActive isSquare={isSquare} />}
                </motion.div>
              )
            })}
          </AnimatePresence>
          <ChipTrayPressArea />
        </div>

        {/* for dev */}
        <Image
          src={Semicircle}
          className="semicircle opacity-0"
          alt="Semicircle decoration"
        />

        <motion.div
          className="oval z-[1]"
          animate={isOpen ? 'open' : 'closed'}
          variants={{
            open: { opacity: 1, scale: 1 },
            closed: { opacity: 0, scale: 0.5 },
          }}
          transition={{
            duration: 0.5,
            type: 'spring',
            stiffness: 256,
            damping: 24,
            mass: 1,
          }}
        >
          <ShineBorder />
        </motion.div>
      </div>
      <FooterAnimation
        type="chip"
        enable={enableAnimation}
      >
        <ClickableArea
          className="z-[2] h-[3.125rem] w-[5.125rem]"
          onClick={toggleSelector}
        >
          <div
            id="ChipBtn"
            className="center relative z-[1] -mb-1 cursor-pointer"
          >
            <FooterChip
              value={selectedChipVal}
              isDisabled={isChipDisabled(selectedChipVal)}
            />
            <ChipTrayAnimation
              position="left"
              enable={enableAnimation}
            >
              <Image
                src={chipTrayChips1}
                className="opacity-1 ml-1 h-[2.15rem]"
                alt="Chip tray"
              />
            </ChipTrayAnimation>
            <ChipTrayAnimation
              position="right"
              enable={enableAnimation}
            >
              <Image
                src={chipTrayChips2}
                className="opacity-1 h-[2.15rem]"
                alt="Chip tray"
              />
            </ChipTrayAnimation>
          </div>
        </ClickableArea>
      </FooterAnimation>
    </div>
  )
}

