import { AnimationProps, HTMLMotionProps } from 'framer-motion'

export interface Position {
  x: number | string
  y: number | string
  z: number | string
}

export interface OpacityValues {
  start: number
  end: number
}

export interface ScaleValues {
  start: number
  end: number
}

export interface AnimationConfig {
  duration?: number
  delay?: number
  ease?: 'linear' | 'easeIn' | 'easeOut' | 'easeInOut'
  type?: 'spring' | 'tween' | 'inertia'
  stiffness?: number
  damping?: number
  mass?: number
}

export interface BaseAnimationProps extends HTMLMotionProps<'div'> {
  isAnimating?: boolean
  animation?: AnimationConfig
  initial?: AnimationProps['initial']
  animate?: AnimationProps['animate']
  styleAfterAnimation?: React.CSSProperties
  onAnimationComplete?: () => void
}

export const defaultAnimation: AnimationConfig = {
  duration: 0.5,
  delay: 0,
  ease: 'easeOut',
}
