import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { chipTextSizeType, useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useEffect, useRef, useState } from 'react'

export default function useChipTextSize(
  targetRef: React.RefObject<HTMLDivElement | null>,
  sizeRatio: number,
  boardName: keyof chipTextSizeType
) {
  const [chipTextSize, setChipTextSize] = useState(0)
  const isRaceTrack = useBettingStore((s) => s.isRaceTrack)
  const previousWidthRef = useRef(0)

  useEffect(() => {
    const targetEle = targetRef.current
    if (!targetEle) return

    let timeoutId: NodeJS.Timeout

    const updateTextSize = (width: number) => {
      const textSize = Number((width * sizeRatio).toFixed(6))
      setChipTextSize(textSize)
      useLayoutStore.setState((prev) => ({ chipTextSize: { ...prev.chipTextSize, [boardName]: textSize } }))
    }

    const processWidth = () => {
      const width = targetEle.clientWidth ?? 0
      if (width !== 0) {
        updateTextSize(width)
      } else {
        timeoutId = setTimeout(getWidth, 300)
      }
    }

    const getWidth = () => {
      if (!targetEle) return

      const anim = targetEle.getAnimations()

      if (!anim.length) return processWidth()

      Promise.all(anim.map((anim) => anim.finished))
        .then(processWidth)
        .catch(processWidth)
    }

    getWidth()

    const resizeObserver = new ResizeObserver((entries) => {
      if (!entries || entries.length === 0) return
      const newWidth = entries[0].contentRect.width

      if (previousWidthRef.current !== newWidth) {
        getWidth()
        previousWidthRef.current = newWidth
      }
    })

    resizeObserver.observe(targetEle)

    return () => {
      resizeObserver.disconnect()
      clearTimeout(timeoutId)
    }
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isRaceTrack, sizeRatio])
  return chipTextSize
}
