import ChipImg from '@repo/ui/components/chipTray/ChipImg'
import { convertLabel } from '@repo/utils/common/bet'
import { cn } from '@repo/utils/common/common'
import { memo } from 'react'

interface ChipProps {
  value: number
  isDisabled: boolean
}

function FooterChip({ value, isDisabled }: ChipProps) {
  const numValue = convertLabel(value)
  return (
    <button
      id="FooterChip"
      className={cn('g-chip', 'absolute mb-[0.2rem] ml-[0.1rem] w-[2.625rem] text-[1.25rem]', { isDisabled })}
    >
      <ChipImg
        val={value}
        isDisabled={isDisabled}
      />

      <div className="stroke">{numValue}</div>
      <div className="text">{numValue}</div>
    </button>
  )
}

export default memo(FooterChip)
