import { ReactNode } from 'react'
import DisplacementContainer, { DisplacementContainerProps } from './DisplacementContainer'
import OpacityContainer from './OpacityContainer'
import ScaleContainer, { ScaleContainerProps } from './ScaleContainer'

interface HeaderAnimationProps {
  displacementProps?: DisplacementContainerProps
  scaleProps?: ScaleContainerProps
  type?: 'balance' | 'clock' | 'totalBet'
  children: ReactNode
  enable?: boolean
}

const HeaderAnimation = ({ children, displacementProps, scaleProps, type, enable = true }: HeaderAnimationProps) => {
  return (
    <DisplacementContainer
      enable={enable}
      startPosition={{ x: 0, y: type === 'clock' ? '100%' : '150%' }}
      endPosition={{ x: 0, y: 0, z: 1 }}
      animation={{ duration: 0.75, delay: 1.25, type: 'spring', stiffness: 250, damping: 60 }}
      className="z-[-1] overflow-hidden"
      {...displacementProps}
    >
      <ScaleContainer
        enable={enable}
        scale={{ start: type === 'clock' ? 0.75 : 0, end: 1 }}
        animation={{ duration: 1.06, delay: 0.94, type: 'spring', stiffness: 250, damping: 60 }}
        {...scaleProps}
      >
        <OpacityContainer
          enable={enable}
          opacity={{ start: 0.75, end: 1 }}
          animation={{ duration: 0.5, delay: 1.5, type: 'spring', stiffness: 250, damping: 60 }}
        >
          {children}
        </OpacityContainer>
      </ScaleContainer>
    </DisplacementContainer>
  )
}

export default HeaderAnimation
