import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useLongPress } from '@uidotdev/usehooks'

import DisplacementContainer from '@repo/ui/components/animatedContainer/DisplacementContainer'
import FooterAnimation from '@repo/ui/components/animatedContainer/FooterAnimation'
import ChipTray from '@repo/ui/components/chipTray/ChipTray'
import ChipTrayVertical from '@repo/ui/components/chipTray/ChipTrayVertical'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn } from '@repo/utils/common/common'
import { memo, useMemo } from 'react'
import FooterIcon from './FooterIcon'

type FooterProps = {
  enableAnimation?: boolean
}

function Footer({ enableAnimation = false }: FooterProps) {
  const toggleMenu = useLayoutStore((s) => s.toggleMenu)
  const isOpen = useLayoutStore((s) => s.isOpenSelector)

  const boardHistory = useCartStore((s) => s.boardHistory)
  const boardHistoryCache = useCartStore((s) => s.boardHistoryCache)
  const remainedBalance = useCartStore((s) => s.remainedBalance)

  const handleDouble = useCartStore((s) => s.handleDouble)
  const handleRepeat = useCartStore((s) => s.handleRepeat)
  const handleUndo = useCartStore((s) => s.handleUndo)

  const pressAttrs = useLongPress(() => useCartStore.setState({ boardHistory: [], totalBet: 0 }), {
    threshold: 1500,
  })

  const cacheSum = boardHistoryCache ? Object.values(boardHistoryCache).reduce((a, b) => a + b, 0) : 0

  const isRepeatDisable = !boardHistoryCache || remainedBalance - cacheSum < 0

  const isBet = useBettingStore((s) => s.isBet)
  const isOverLimit = useBettingStore((s) => s.isOverLimit)
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  const DeviceContainer = useMemo(
    () =>
      ({ children, type }: { children: React.ReactNode; type: 'chat' | 'undo' | 'chip' | 'doubleRepeatBtn' | 'hamburger' }) => {
        return isSmallDevice ? (
          <>{children}</>
        ) : (
          <FooterAnimation
            type={type}
            enable={enableAnimation}
          >
            {children}
          </FooterAnimation>
        )
      },
    [enableAnimation, isSmallDevice]
  )

  return (
    <DisplacementContainer
      className="flex flex-col overflow-hidden"
      startPosition={{ x: -0.001, y: 0 }}
      endPosition={{ x: 0, y: 0 }}
      animation={{ duration: 1.54, delay: 0 }}
      styleAfterAnimation={{ overflow: 'visible', zIndex: 3 }}
    >
      <div
        id="Footer"
        className={cn('relative mt-[.275rem] flex h-11 w-full select-none items-center pb-[.4188rem]', isOpen ? 'z-[11]' : 'z-[3]', {
          isSmallDevice,
        })}
      >
        <DeviceContainer type="chat">
          <FooterIcon
            animation={enableAnimation ? 'default' : 'none'}
            type="chat"
            onClick={() => {
              toggleMenu('chat')
            }}
          />
        </DeviceContainer>
        <DeviceContainer type="undo">
          <FooterIcon
            {...pressAttrs}
            animation={enableAnimation ? 'default' : 'none'}
            id="MainUndoBtn"
            disable={boardHistory.length === 0 || !isBet}
            type="undo"
            onClick={handleUndo}
          />
        </DeviceContainer>

        {isSmallDevice ? <ChipTrayVertical enableAnimation={enableAnimation} /> : <ChipTray enableAnimation={enableAnimation} />}

        <DeviceContainer type="doubleRepeatBtn">
          <div
            className="center"
            id="doubleRepeatBtn"
          >
            {boardHistory.length > 0 && (
              <FooterIcon
                animation={enableAnimation ? 'default' : 'none'}
                type="twice"
                disable={!isBet || isOverLimit}
                onClick={handleDouble}
              />
            )}
            {boardHistory.length <= 0 && (
              <FooterIcon
                animation={enableAnimation ? 'default' : 'none'}
                type="repeat"
                disable={!isBet || isRepeatDisable}
                onClick={handleRepeat}
              />
            )}
          </div>
        </DeviceContainer>

        <DeviceContainer type="hamburger">
          <FooterIcon
            animation={enableAnimation ? 'default' : 'none'}
            type="hamburger"
            className="h-12 w-[2.9rem]"
            onClick={() => toggleMenu('index')}
          />
        </DeviceContainer>
      </div>
    </DisplacementContainer>
  )
}

export default memo(Footer)
