import { useTranslation } from 'react-i18next'

import Image from '@repo/ui/components/common/Image'
import { cn } from '@repo/utils/common/common'

import { BetModeType, useBettingStore } from '@repo/ui/store/useBettingStore'
import Default from '@roulette/assets/autoPlay/mobile/default.webp'
import FullComplete from '@roulette/assets/autoPlay/mobile/fullComplete.webp'
import StraightUp from '@roulette/assets/autoPlay/mobile/straightUp.webp'
import './BettingMode.scss'

export default function MobileBettingMode() {
  const { t } = useTranslation()
  const betMode = useBettingStore((s) => s.betMode)

  const toggleType = (betMode: BetModeType) => {
    useBettingStore.setState({ betMode })
  }

  const getBettingModeImage = (betMode: BetModeType) => {
    if (betMode === 'straight') return StraightUp
    if (betMode === 'fullComplete') return FullComplete
    return Default
  }

  return (
    <div
      className="px-[0.875rem] pb-6"
      id="BettingMode"
    >
      <div className="relative mb-6 mt-[0.7rem] flex px-[0.125rem]">
        <Image
          src={getBettingModeImage(betMode)}
          alt="Betting mode"
        />

        <div
          className={cn('box left-0 w-[34.1%]', { active: betMode === 'default' })}
          onClick={() => toggleType('default')}
        >
          <span>{t('COMMON.MODE_DEFAULT')}</span>
        </div>
        <div
          className={cn('box left-[33%] w-[34%]', { active: betMode === 'straight' })}
          onClick={() => toggleType('straight')}
        >
          <span>{t('COMMON.MODE_STRAIGHT')}</span>
        </div>
        <div
          className={cn('box right-0 w-[33.9%]', { active: betMode === 'fullComplete' })}
          onClick={() => toggleType('fullComplete')}
        >
          <span>{t('COMMON.MODE_FULL_COMPLETE')}</span>
        </div>
      </div>
    </div>
  )
}
