import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn, thousandSeparator } from '@repo/utils/common/common'
import React, { memo, useRef } from 'react'
import { useTranslation } from 'react-i18next'

import DisplacementContainer from '@repo/ui/components/animatedContainer/DisplacementContainer'
import HeaderAnimation from '@repo/ui/components/animatedContainer/HeaderAnimation'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useWinNumberStore } from '@repo/ui/store/useWinNumberStore'
import useHeaderFontSize from '@repo/utils/hook/useHeaderFontSize'
import './Header.scss'

import Clock from '../Clock'

const Balance = memo(function () {
  const { remainedBalance, currencySymbol } = useCartStore((s) => ({
    remainedBalance: s.remainedBalance,
    currencySymbol: s.currencySymbol,
  }))
  const mobileHeaderFontSize = useLayoutStore((s) => s.mobileHeaderFontSize)
  const { t } = useTranslation()

  return (
    <span
      id="MainBalance"
      className="center gap-[0.2rem] whitespace-nowrap text-[0.688rem] text-[#f4EFE8]"
      style={{ fontSize: `${mobileHeaderFontSize}rem` }}
    >
      <span className="pairLabel">{t('COMMON.BALANCE')}:</span>
      <span className="pairNumber">{`${currencySymbol} ${thousandSeparator(remainedBalance)}`}</span>
    </span>
  )
})

const TotalBet = memo(() => {
  const mobileHeaderFontSize = useLayoutStore((s) => s.mobileHeaderFontSize)
  const totalBet = useCartStore((s) => s.totalBet)
  const lastWinCache = useWinNumberStore((s) => s.lastWinCache)

  const { t } = useTranslation()
  const currencySymbol = useCartStore((s) => s.currencySymbol)

  return (
    <span
      id="TotalBet"
      className="center gap-[.2188rem] whitespace-nowrap text-[0.688rem] text-[#C9AE8D]"
      style={{ fontSize: `${mobileHeaderFontSize}rem` }}
    >
      <span className="pairLabel">{lastWinCache ? t('COMMON.LAST_WIN') : t('COMMON.TOTAL_BET')}:</span>
      <span className="pairNumber pr-[.0313rem]">
        {`${currencySymbol} ${lastWinCache ? thousandSeparator(lastWinCache) : thousandSeparator(totalBet)}`}
      </span>
    </span>
  )
})

const Clone = memo(() => {
  const cloneRef = useRef<HTMLSpanElement>(null)
  const { remainedBalance, currencySymbol } = useCartStore((s) => ({
    remainedBalance: s.remainedBalance,
    currencySymbol: s.currencySymbol,
  }))
  const totalBet = useCartStore((s) => s.totalBet)
  const { t } = useTranslation()
  useHeaderFontSize(cloneRef)

  return (
    <span
      id="Clone"
      ref={cloneRef}
      className="center invisible absolute gap-[.2188rem] whitespace-nowrap text-[0.688rem] text-[#f4EFE8]"
    >
      <span className="pairLabel">{t('COMMON.TOTAL_BET')}:</span>
      <span className="pairNumber pr-[.0313rem]">{`${currencySymbol} ${thousandSeparator(Math.max(remainedBalance, totalBet))}`}</span>
    </span>
  )
})

interface DeviceContainerProps {
  children: React.ReactNode
  type: 'balance' | 'clock' | 'totalBet'
  enableAnimation: boolean
}

const DeviceContainer = memo(({ children, type, enableAnimation }: DeviceContainerProps) => {
  return (
    <HeaderAnimation
      type={type}
      enable={enableAnimation}
    >
      {children}
    </HeaderAnimation>
  )
})

interface HeaderProps {
  betResult?: React.ReactNode
  enableAnimation?: boolean
}

function Header({ betResult, enableAnimation = false }: HeaderProps) {
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  return (
    <DisplacementContainer
      id="Header"
      className={cn('z-[0] flex w-full flex-col px-2', { 'fixed top-0 px-0 pr-5': isSmallDevice })}
      startPosition={{ x: 0, y: -0.001 }}
      endPosition={{ x: 0, y: 0 }}
      animation={{ duration: 2.2, delay: 0 }}
    >
      {betResult && <div className="mr-[-0.5rem]">{betResult}</div>}
      <div
        id="HeaderBar"
        className="relative flex w-full items-center justify-between py-[.125rem]"
      >
        <Clone />
        <DeviceContainer
          type="balance"
          enableAnimation={enableAnimation}
        >
          <Balance />
        </DeviceContainer>
        <span className="absolute left-1/2 -translate-x-1/2">
          <DeviceContainer
            type="clock"
            enableAnimation={enableAnimation}
          >
            <Clock />
          </DeviceContainer>
        </span>

        <DeviceContainer
          type="totalBet"
          enableAnimation={enableAnimation}
        >
          <TotalBet />
        </DeviceContainer>
      </div>
    </DisplacementContainer>
  )
}

export default memo(Header)
