import { JSX } from 'react'
import { useTranslation } from 'react-i18next'

import AutoPlay from '@repo/ui/components/menu/common/bettingMode/AutoPlay'
import ChatRoom, { ChatHeader } from '@repo/ui/components/menu/common/chat/ChatRoom'
import Layout from '@repo/ui/components/menu/common/footer/FooterLayout'
import GameLimit from '@repo/ui/components/menu/common/game-rules/GameLimit'
import GameRule from '@repo/ui/components/menu/common/game-rules/GameRule'
import GameRuleInfoComponent from '@repo/ui/components/menu/common/game-rules/GameRuleInfo'
import GameHistory from '@repo/ui/components/menu/common/history/GameHistory'
import GameHistoryContent, { GameHistoryContentHeader } from '@repo/ui/components/menu/common/history/GameHistoryContent'
import SafariHint from '@repo/ui/components/menu/common/safari/SafariHint'
import Setting from '@repo/ui/components/menu/common/setting/Setting'
import SettingLanguage from '@repo/ui/components/menu/common/setting/SettingLanguage'
import GameMenu from '@repo/ui/components/menu/mobile/GameMenu'
import { RoomType } from '@repo/ui/store/useChatStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useDataStore } from '@repo/ui/store/useDataStore'
import { MenuPageType, useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { GameRules } from '@repo/ui/utils/componentResolver'
import { cn } from '@repo/utils/common/common'

export type ExtendedMenuType = {
  [K in Exclude<MenuPageType, null>]?: React.ReactNode
}

type BaseFooterMenuProps = {
  extendedMenu: ExtendedMenuType
  settingContent?: {
    gameSpecificsElement?: JSX.Element
  }
}

export default function BaseFooterMenu(props: BaseFooterMenuProps) {
  const { extendedMenu, settingContent } = props
  const menuPage = useLayoutStore((state) => state.menuPage)
  const titleList = useDataStore((state) => state.titleList)
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  const { t } = useTranslation()

  const menuComponents = {
    history: (
      <Layout
        header={t('COMMON.GAME_HISTORY')}
        content={<GameHistory />}
      />
    ),
    history_content: (
      <Layout
        header={<GameHistoryContentHeader />}
        content={<GameHistoryContent />}
        scrollWrapClassName="pb-[3.5rem]"
      />
    ),
    limits: (
      <Layout
        header={t('COMMON.PAYOUTS_LIMITS')}
        content={<GameLimit />}
      />
    ),
    chat: (
      <Layout
        header={<ChatHeader />}
        isHeaderMultiLineText={true}
        content={<ChatRoom type={RoomType.Live} />}
      />
    ),
    chat_support: (
      <Layout
        header={t('COMMON.SUPPORT')}
        content={<ChatRoom type={RoomType.Support} />}
      />
    ),
    setting: (
      <Layout
        header={t('COMMON.SETTINGS')}
        content={<Setting gameSpecificsElement={settingContent?.gameSpecificsElement} />}
        scrollWrapClassName="pb-[2.5rem]"
      />
    ),
    language: (
      <Layout
        header={t('COMMON.LANGUAGE')}
        content={<SettingLanguage />}
        scrollWrapClassName="pb-[3rem]"
      />
    ),
    safari_hint: (
      <Layout
        content={<SafariHint />}
        hideScrollHint
      />
    ),
    autoplay: (
      <Layout
        header={t('COMMON.AUTOPLAY')}
        content={<AutoPlay requireTitle={false} />}
        leftIcon={false}
      />
    ),
    rules: (
      <Layout
        header={t('COMMON.HELP_RULES')}
        content={<GameRule titleList={titleList} />}
        scrollWrapClassName="pb-[2.6875rem]"
      />
    ),
    rules_inner: (
      <Layout
        header={t('COMMON.GAMES_RULES')}
        content={
          <GameRuleInfoComponent>
            <GameRules />
          </GameRuleInfoComponent>
        }
        scrollWrapClassName="pb-[2.6875rem]"
      />
    ),

    default: (
      <Layout
        scrollWrapClassName={cn('max-h-[25.91875rem] pb-[3.6rem]', {
          'max-h-[25.29569rem] pb-[3rem]': isSmallDevice,
        })}
        content={<GameMenu />}
      />
    ),
    ...extendedMenu,
  }

  return menuComponents[menuPage as keyof typeof menuComponents] || menuComponents.default
}
