import { motion } from 'framer-motion'
import { useState } from 'react'
import { BaseAnimationProps, defaultAnimation, OpacityValues } from '../../utils/types/animationContainerType'

export interface OpacityContainerProps extends BaseAnimationProps {
  opacity?: Partial<OpacityValues>
  enable?: boolean
}

const defaultOpacity: OpacityValues = {
  start: 0,
  end: 1,
}

const OpacityContainer = ({
  children,
  opacity = defaultOpacity,
  animation = defaultAnimation,
  isAnimating = true,
  initial,
  animate,
  style,
  styleAfterAnimation,
  onAnimationComplete,
  enable = true,
  ...rest
}: OpacityContainerProps) => {
  const [animationEnded, setAnimationEnded] = useState(false)

  const defaultInitial = { opacity: opacity.start ?? defaultOpacity.start }
  const defaultAnimate = isAnimating ? { opacity: opacity.end ?? defaultOpacity.end } : { opacity: opacity.start ?? defaultOpacity.start }

  const handleAnimationComplete = () => {
    setAnimationEnded(true)
    onAnimationComplete?.()
  }

  if (!enable) return <>{children}</>

  return (
    <motion.div
      initial={initial || defaultInitial}
      animate={animate || defaultAnimate}
      transition={{ ...animation }}
      onAnimationComplete={handleAnimationComplete}
      style={{
        position: 'relative',
        ...style,
        ...(animationEnded ? styleAfterAnimation : {}),
      }}
      {...rest}
    >
      {children}
    </motion.div>
  )
}

export default OpacityContainer
