import { motion } from 'framer-motion'
import { useState } from 'react'
import { BaseAnimationProps, defaultAnimation, Position } from '../../utils/types/animationContainerType'

export interface DisplacementContainerProps extends BaseAnimationProps {
  startPosition?: Partial<Position>
  endPosition?: Partial<Position>
  enable?: boolean
}

const defaultPosition: Position = {
  x: 0,
  y: 0,
  z: 0,
}

const DisplacementContainer = ({
  children,
  startPosition = defaultPosition,
  endPosition = defaultPosition,
  animation = defaultAnimation,
  isAnimating = true,
  initial,
  animate,
  style,
  styleAfterAnimation,
  onAnimationComplete,
  enable = true,
  ...rest
}: DisplacementContainerProps) => {
  const [animationEnded, setAnimationEnded] = useState(false)

  const defaultInitial = {
    x: startPosition.x ?? defaultPosition.x,
    y: startPosition.y ?? defaultPosition.y,
    z: startPosition.z ?? defaultPosition.z,
  }

  const defaultAnimate = isAnimating
    ? {
        x: endPosition.x ?? defaultPosition.x,
        y: endPosition.y ?? defaultPosition.y,
      }
    : {
        x: startPosition.x ?? defaultPosition.x,
        y: startPosition.y ?? defaultPosition.y,
      }

  const handleAnimationComplete = () => {
    setAnimationEnded(true)
    onAnimationComplete?.()
  }

  /*
   * Remove the guard clause to prevent loss of state, re-running of effects, unwanted remounts, and visual disruption.
   * Use motionProps to control the animation and make wrapper persistent.
   */
  // if (!enable) return <>{children}</>

  const motionProps = enable
    ? {
        initial: initial || defaultInitial,
        animate: animate || defaultAnimate,
        transition: { ...animation },
        onAnimationComplete: handleAnimationComplete,
      }
    : {
        initial: false,
        animate: false,
      }

  return (
    <motion.div
      style={{
        ...(enable && animationEnded
          ? {
              zIndex: endPosition.z ?? defaultPosition.z,
              ...styleAfterAnimation,
            }
          : { ...style }),
      }}
      {...motionProps}
      {...rest}
    >
      {children}
    </motion.div>
  )
}

export default DisplacementContainer
