import React, { JSX, useMemo } from 'react'
import { useTranslation } from 'react-i18next'

import Toggle from '@repo/ui/components/common/toggle/Toggle'
import { Icon } from '@repo/ui/icon'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useSettingStore } from '@repo/ui/store/useSettingStore'
import { playSound } from '@repo/utils/common/sound'
import { LANGUAGES } from '@repo/utils/locales/constants'

import ClickableArea from '../../common/clickableArea/ClickableArea'

export default function MenuBottom({ menus }: { menus: string[] }) {
  const { t, i18n } = useTranslation()
  const masterVolume = useSettingStore((s) => s.masterVolume)
  const toggleAllVolume = useSettingStore((s) => s.toggleAllVolume)
  const exitGame = useConfigStore((s) => s.exitGame)

  const selectedLanguageDisplay = useMemo(
    () => LANGUAGES.find((item) => item.code === (i18n.resolvedLanguage ?? i18n.language))?.name || LANGUAGES[0].name,
    [i18n.resolvedLanguage, i18n.language]
  )

  const muted = masterVolume === 0

  // Menu item renderers
  const menuRenderers: Record<string, () => JSX.Element | null> = {
    gameSettings: () => (
      <div
        className="flex h-[3.75rem] items-center justify-between self-stretch border-t-[0.0625rem] border-[#252525]"
        onClick={() => {
          useLayoutStore.setState({ menuPage: 'setting' })
          playSound(['click'])
        }}
      >
        <div className="flex items-center gap-2 text-[0.875rem] font-normal leading-5 tracking-[-0.0262rem] text-[#f9f9f9]">
          <Icon.Setting className="w-5" />
          {t('COMMON.GAME_SETTINGS')}
        </div>
        <Icon.RightGolden className="w-4" />
      </div>
    ),

    language: () => (
      <div
        className="flex h-[3.75rem] justify-between self-stretch border-t-[0.0625rem] border-[#252525]"
        onClick={() => {
          useLayoutStore.setState({ menuPage: 'language' })
          playSound(['click'])
        }}
      >
        <div className="flex items-center gap-2 text-[0.875rem] font-normal leading-5 tracking-[-0.0262rem] text-[#f9f9f9]">
          <Icon.Language className="w-5" />
          {t('COMMON.LANGUAGE')}
        </div>
        <div className="flex items-center gap-[0.125rem] pt-[0.08rem]">
          <div className="g-text-golden overflow-hidden text-ellipsis whitespace-nowrap pt-[0.1rem] text-xs font-normal uppercase leading-4 tracking-[.045rem]">
            {selectedLanguageDisplay}
          </div>
          <Icon.RightGolden className="w-4" />
        </div>
      </div>
    ),

    sound: () => (
      <div className="flex h-[3.75rem] items-center justify-between self-stretch border-t-[0.0625rem] border-[#252525]">
        <div className="flex items-center gap-2 text-[0.875rem] font-normal leading-5 tracking-[-0.0262rem] text-[#f9f9f9]">
          {muted ? <Icon.SoundOff className="w-5" /> : <Icon.Sound className="w-5" />}
          {t('COMMON.SOUND')}
        </div>
        <ClickableArea
          className="size-[3.125rem]"
          onClick={() => {
            playSound(['click'])
            toggleAllVolume()
          }}
        >
          <Toggle checked={!muted} />
        </ClickableArea>
      </div>
    ),

    help: () => (
      <div
        className="flex h-[3.75rem] items-center justify-between self-stretch border-t-[0.0625rem] border-[#252525]"
        onClick={() => {
          useLayoutStore.setState({ menuPage: 'rules' })
          playSound(['click'])
        }}
      >
        <div className="flex items-center gap-2 text-[0.875rem] font-normal leading-5 tracking-[-0.0262rem] text-[#f9f9f9]">
          <Icon.Help className="w-[1.3125rem]" />
          {t('COMMON.HELP')}
        </div>
        <Icon.RightGolden className="w-4" />
      </div>
    ),

    exit: () => (
      <div
        className="flex h-[3.75rem] items-center justify-between self-stretch border-t-[0.0625rem] border-[#252525]"
        onClick={exitGame}
      >
        <div className="flex items-center gap-2 text-[0.875rem] font-normal leading-5 tracking-[-0.0262rem] text-[#f9f9f9]">
          <Icon.Exit className="w-[1.3125rem]" />
          {t('COMMON.EXIT')}
        </div>
      </div>
    ),
  }

  return menus.map((menu) => {
    const renderMenu = menuRenderers[menu]
    return renderMenu ? <React.Fragment key={menu}>{renderMenu()}</React.Fragment> : null
  })
}
