import { ReactNode, useMemo } from 'react'
import DisplacementContainer, { DisplacementContainerProps } from './DisplacementContainer'

interface FooterAnimationProps extends DisplacementContainerProps {
  children: ReactNode
  type: 'chat' | 'undo' | 'chip' | 'doubleRepeatBtn' | 'hamburger'
  enable?: boolean
}

const FooterAnimation = ({ children, type, enable = true, ...rest }: FooterAnimationProps) => {
  const delay = useMemo(() => {
    if (type === 'chat' || type === 'hamburger') return 0.83
    if (type === 'undo' || type === 'doubleRepeatBtn') return 0.78
    if (type === 'chip') return 0.75
    return 0.75
  }, [type])

  const duration = useMemo(() => {
    if (type === 'chat' || type === 'hamburger') return 0.72
    if (type === 'undo' || type === 'doubleRepeatBtn') return 0.68
    if (type === 'chip') return 0.62
    return 0.62
  }, [type])

  return (
    <DisplacementContainer
      enable={enable}
      startPosition={{ x: 0, y: type === 'chip' ? '-150%' : '-100%' }}
      endPosition={{ x: 0, y: '0%', z: 1 }}
      animation={{ duration, delay, type: 'spring', stiffness: 250, damping: 60 }}
      {...rest}
    >
      {children}
    </DisplacementContainer>
  )
}

export default FooterAnimation
