import { motion } from 'framer-motion'
import { useState } from 'react'
import { BaseAnimationProps, defaultAnimation, ScaleValues } from '../../utils/types/animationContainerType'

export interface ScaleContainerProps extends BaseAnimationProps {
  scale?: Partial<ScaleValues>
  enable?: boolean
}

const defaultScale: ScaleValues = {
  start: 1,
  end: 1,
}

const ScaleContainer = ({
  children,
  scale = defaultScale,
  animation = defaultAnimation,
  isAnimating = true,
  initial,
  animate,
  style,
  styleAfterAnimation,
  onAnimationComplete,
  enable = true,
  ...rest
}: ScaleContainerProps) => {
  const [animationEnded, setAnimationEnded] = useState(false)

  const defaultInitial = { scale: scale.start ?? defaultScale.start }
  const defaultAnimate = isAnimating ? { scale: scale.end ?? defaultScale.end } : { scale: scale.start ?? defaultScale.start }

  const handleAnimationComplete = () => {
    setAnimationEnded(true)
    onAnimationComplete?.()
  }

  if (!enable) return <>{children}</>

  return (
    <motion.div
      initial={initial || defaultInitial}
      animate={animate || defaultAnimate}
      transition={{ ...animation }}
      onAnimationComplete={handleAnimationComplete}
      style={{
        position: 'relative',
        transformOrigin: 'center',
        ...style,
        ...(animationEnded ? styleAfterAnimation : {}),
      }}
      {...rest}
    >
      {children}
    </motion.div>
  )
}

export default ScaleContainer
