import NetworkStateModal from '@repo/ui/components/common/modal/NetworkStateModal'
import CountDownCap from '@repo/ui/components/countDownCap/CountDownCap'
import Footer from '@repo/ui/components/layout/footer/mobile/Footer'
import Header from '@repo/ui/components/layout/header/mobile/Header'
import MainBgImage from '@repo/ui/components/layout/MainBgImage'
import ToastList from '@repo/ui/components/toast/ToastList'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn } from '@repo/utils/common/common'
import useKeyboardMargin from '@repo/utils/hook/useKeyboardMargin'

import overlayBg from '@roulette/assets/overlay_bg.webp'
import BetResult from '@roulette/components/header/BetResult'
import FooterMenu from '@roulette/components/layout/menu/FooterMenu'
import VideoStream from '@roulette/components/video/VideoStream'
import GameContent from './GameContent'

const MobileLayout = () => {
  const mainBoardMb = useKeyboardMargin()
  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)
  const marginBottom = isSmallDevice ? `calc(0.35rem + ${mainBoardMb}px)` : `${mainBoardMb}px`
  const IS_CLOSE_SERVER = useConfigStore((s) => s.IS_CLOSE_SERVER)

  return (
    <div
      id="MobileLayout"
      className={cn({ isSmallDevice })}
    >
      <Header betResult={<BetResult />} />
      <div
        id="MainBoard"
        className={cn(`mx-auto mt-[.0625rem] flex-1 rounded-[2rem] pb-[1.95rem]`, {
          'mt-[2.1rem] rounded-[1.5rem]': isSmallDevice,
        })}
        style={{ marginBottom }}
      >
        <MainBgImage
          overlayBg={overlayBg}
          imageClassName="mt-9"
        />
        <CountDownCap />
        <ToastList />
        <VideoStream />
        <GameContent />
        <FooterMenu />
        <NetworkStateModal isCloseServer={IS_CLOSE_SERVER} />
      </div>
      <Footer />
    </div>
  )
}

export default MobileLayout
