import React, { memo, useEffect, useRef, useState } from 'react'

import { Icon } from '@repo/ui/icon'
import { skipScrollPages, useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { scrollWrapToTop } from '@repo/ui/utils/misc'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import usePreventPullToRefresh from '@repo/utils/hook/usePreventPullToRefresh'

import './FooterLayout.scss'

type FooterLayoutProps = {
  header?: React.ReactNode | string
  isHeaderMultiLineText?: boolean
  footer?: React.ReactNode
  content: React.ReactNode
  hideScrollHint?: boolean
  contentWrapClassName?: string
  scrollWrapClassName?: string
  requireContentShadow?: boolean
  leftIcon?: boolean
}

function FooterLayout(props: FooterLayoutProps) {
  const { header, isHeaderMultiLineText = false, content, footer, hideScrollHint, contentWrapClassName, scrollWrapClassName } = props
  const { requireContentShadow = false, leftIcon = true } = props

  const { menuPage, closeMenu, goBackMenu, getIsFullHeight } = useLayoutStore()

  const scrollWrapRef = useRef<HTMLDivElement>(null)
  usePreventPullToRefresh(scrollWrapRef)

  const isOpenMenu = menuPage !== null
  const isFull = getIsFullHeight() && isOpenMenu

  const [isBottom, setIsBottom] = useState(false)
  const handleScroll = (e: React.UIEvent<HTMLDivElement>) => {
    const target = e.target as HTMLDivElement

    const scrollEnd = target.scrollHeight - target.scrollTop === target.clientHeight
    if (isBottom !== scrollEnd) setIsBottom(scrollEnd)
  }

  const renderLeftIcon = () => {
    if (leftIcon) {
      return (
        <Icon.Left
          className="size-11 cursor-pointer"
          onClick={() => {
            goBackMenu()
            playSound(['click'])
          }}
        />
      )
    }
    return <div className="size-11"></div>
  }

  // Scroll to top when menuPage changes
  useEffect(() => {
    if (!menuPage || skipScrollPages.includes(menuPage)) return
    scrollWrapToTop()
  }, [menuPage])

  return (
    <>
      <div
        id="FooterMenu"
        className={cn({ isOpenMenu, isFull })}
        data-modal-type={menuPage}
      >
        {header && (
          <div
            className={cn('flex items-center justify-between px-4 pb-[0.0625rem] pt-4', {
              'pb-4': isHeaderMultiLineText,
            })}
          >
            {renderLeftIcon()}
            {typeof header === 'string' ? <div className="goldenHeader ml-[0.1rem] mt-[0.1rem]">{header}</div> : header}

            <Icon.Close
              className="size-11 cursor-pointer"
              onClick={() => {
                playSound(['click'])
                closeMenu()
              }}
            />
          </div>
        )}
        <div className={cn('contentWrap', contentWrapClassName)}>
          <div
            ref={scrollWrapRef}
            className={cn('scrollWrap', scrollWrapClassName)}
            onScroll={handleScroll}
          >
            {content}
          </div>
          {requireContentShadow && <div className="scrollShadow" />}
          {!hideScrollHint && (
            <div className={cn('h-[5rem]', 'scrollShadow', 'transition-opacity', 'pointer-events-none', { 'opacity-0': isBottom })}></div>
          )}
        </div>
        {footer}
      </div>
      <div
        className="FooterMenuMask"
        onClick={() => {
          playSound(['click'])
          closeMenu()
        }}
      />
    </>
  )
}

export default memo(FooterLayout)
