import { FC, Fragment } from 'react'
import { useTranslation } from 'react-i18next'

import { NotificationHint } from '@repo/ui/components/common/hint/NotificationHint'
import { Icon } from '@repo/ui/icon'
import { tooltipEventBus } from '@repo/ui/store/tooltipEvent'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { MenuPageType, useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useSettingStore } from '@repo/ui/store/useSettingStore'
import { playSound } from '@repo/utils/common/sound'

type MenuButtonProps = {
  icon: keyof typeof Icon
  label: string
  textClassName?: string
  menuPage?: MenuPageType
  onClick?: () => void
}

function MenuButton(props: MenuButtonProps) {
  const { icon, label, textClassName, menuPage, onClick } = props

  const isSmallDevice = useConfigStore((s) => s.IS_SMALL_DEVICE)

  const masterVolume = useSettingStore((s) => s.masterVolume)
  const isSupport = menuPage === 'chat_support'
  const isSound = icon === 'Sound'
  const muted = masterVolume === 0
  const IconComponent = Icon[icon]
  const id = `mobileMenuButton${icon}`

  const iconElement = <IconComponent className="h-7 w-7" />
  const soundIcon = muted ? <Icon.SoundOff className="w-5" /> : <Icon.Sound className="w-5" />

  const comingSoon = () => tooltipEventBus.emit('comingSoon', { anchorSelect: `#${id}` })

  return (
    <div
      id={id}
      className={`relative flex cursor-pointer flex-col items-center gap-[0.4rem]`}
      onClick={onClick ? onClick : comingSoon}
    >
      {isSound ? soundIcon : iconElement}
      {isSupport && <NotificationHint className={`${isSmallDevice ? 'right-[1.5rem]' : 'right-[2rem]'} top-[-0.75rem]`}></NotificationHint>}
      <div className={`text-xs font-normal tracking-[-0.015rem] ${textClassName ?? ''}`}>{label}</div>
    </div>
  )
}

// Create a factory function for menu buttons
const createMenuButton = (icon: keyof typeof Icon, translationKey: string, menuPage?: MenuPageType): FC => {
  const ButtonComponent: FC = () => {
    const { t } = useTranslation()

    return (
      <MenuButton
        icon={icon}
        label={t(translationKey)}
        menuPage={menuPage}
        onClick={
          menuPage
            ? () => {
                useLayoutStore.setState({ menuPage })
                playSound(['click'])
              }
            : undefined
        }
      />
    )
  }

  return ButtonComponent
}

// Create button components using the factory function
const buttons: Record<string, () => FC> = {
  lobby: () => createMenuButton('Lobby', 'COMMON.LOBBY'),
  statistics: () => createMenuButton('Statistics', 'COMMON.STATISTICS', 'statistics'),
  payTable: () => createMenuButton('PayTable', 'COMMON.PAYTABLE', 'limits'),
  history: () => createMenuButton('History', 'COMMON.HISTORY', 'history'),
  chat: () => createMenuButton('ChatButton', 'COMMON.CHAT', 'chat'),
  support: () => createMenuButton('HeadPhones', 'COMMON.SUPPORT', 'chat_support'),
  gameSettings: () => createMenuButton('Setting', 'COMMON.GAME_SETTINGS', 'setting'),
  language: () => createMenuButton('Language', 'COMMON.LANGUAGE', 'language'),
  sound: () => createMenuButton('Sound', 'COMMON.SOUND'),
  help: () => createMenuButton('Help', 'COMMON.HELP', 'rules'),
  exit: () => createMenuButton('Exit', 'COMMON.EXIT'),
}

export default function MenuTop({ menus }: { menus: string[] }) {
  return menus.map((menu, index) => {
    const ButtonComponent = buttons[menu]?.()
    if (!ButtonComponent) return null

    // Only skip divider for specific indices (2 and 5)
    const shouldShowDivider = index !== 2 && index !== 5

    return (
      <Fragment key={index}>
        <ButtonComponent />
        {shouldShowDivider && <div className="my-[0.3125rem] bg-[#323232]" />}
      </Fragment>
    )
  })
}
