import safariHint from '@repo/ui/assets/hint/safariHint.webp'

import Image from '@repo/ui/components/common/Image'
import { Icon } from '@repo/ui/icon'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { memo, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import './SafariHint.scss'

function SafariHint() {
  const closeMenu = useLayoutStore().closeMenu
  const { t } = useTranslation()

  useEffect(() => {
    localStorage.setItem('safariLaunch', ' ')
  }, [])

  return (
    <div
      id="SafariHint"
      className="flex w-full flex-col items-center px-4 pb-6 pt-4"
    >
      <div className="goldenHeader flex items-center justify-between self-stretch">
        <div className="w-8"></div>
        <span>{t('COMMON.SAFARI_HINT1')}</span>
        <Icon.Close
          onClick={closeMenu}
          className="w-8 cursor-pointer"
        />
      </div>

      <div className="mb-[1.1875rem] w-[8.9375rem] text-center text-xs leading-4 tracking-[-0.015rem] text-[#B0B0B0]">
        {t('COMMON.SAFARI_HINT2')}
      </div>
      <div className="mb-[1.1875rem] flex items-center">
        <Image
          className="ml-[-0.7rem] h-[16.0625rem]"
          src={safariHint}
          alt="Safari Hint"
        />
      </div>
      <div
        className="btn cursor-pointer"
        onClick={closeMenu}
      >
        <div className="inner">{t('COMMON.SKIP')}</div>
      </div>
    </div>
  )
}

export default memo(SafariHint)
