import Layout from '@repo/ui/components/menu/common/footer/FooterLayout'
import BaseFooterMenu, { ExtendedMenuType } from '@repo/ui/components/menu/mobile/BaseFooterMenu'

import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useTranslation } from 'react-i18next'

import MobileBettingMode from '@roulette/components/layout/menu/bettingMode/MobileBettingMode'
import Statistic from '@roulette/components/layout/menu/statistics/Statistic'
import StatisticFooter from '@roulette/components/layout/menu/statistics/StatisticFooter'
import { useStatisticStore } from '@roulette/stores/useStatisticStore'

export default function FooterMenu() {
  const { t } = useTranslation()
  const menuPage = useLayoutStore((state) => state.menuPage)
  const activeStatisticTab = useStatisticStore((state) => state.activeTab)
  const requireContentShadow = menuPage === 'statistics' && activeStatisticTab === 'last500'
  const extendedMenu: ExtendedMenuType = {
    betting_mode: (
      <Layout
        header={t('ROULETTE.BETTING_MODE')}
        leftIcon={false}
        content={<MobileBettingMode />}
      />
    ),
    statistics: (
      <Layout
        header={t('COMMON.STATISTICS')}
        footer={<StatisticFooter />}
        content={<Statistic />}
        hideScrollHint
        requireContentShadow={requireContentShadow}
      />
    ),
  }

  return <BaseFooterMenu extendedMenu={extendedMenu} />
}
