import { motion } from 'framer-motion'
import { ReactElement, useRef } from 'react'

import Image from '@repo/ui/components/common/Image'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn } from '@repo/utils/common/common'
import useChipTextSize from '@repo/utils/hook/useChipTextSize'

import bettingGrid from '@roulette/assets/board/mobile/grid.webp'
import mobileGridLowerThick from '@roulette/assets/board/mobile/mobileGridLowerThick.webp'
import mobileGridUpperThick from '@roulette/assets/board/mobile/mobileGridUpperThick.webp'
import { gridBoardMoveVariants, gridBoardTiltCompactVariants, gridBoardTiltVariants } from '@roulette/service/bet'
import BetGrid from './BetGrid'
import './GridBoard.scss'

const Wrap = (props: { children: ReactElement | ReactElement[] }) => {
  const isRaceTrack = useBettingStore((state) => state.isRaceTrack)
  const targetRef = useRef(null)
  const isCompact = useConfigStore((state) => state.IS_SMALL_DEVICE)
  const isSlope = useBettingStore((state) => !state.isBet)
  useChipTextSize(targetRef, 0.0511, 'gridBoard')

  return (
    <motion.div
      id="GridBoard"
      initial={'default'}
      animate={!isRaceTrack ? 'default' : 'hide'}
      variants={gridBoardMoveVariants}
      ref={targetRef}
      className={cn({ isSlope: isSlope, isCompact: isCompact })}
    >
      {/* Dummy element to grab the top line of the tilted board */}
      <span
        data-stream-tip
        className="pointer-events-none absolute left-0 top-0 h-full w-full origin-bottom"
        style={{
          transform: ((isCompact ? gridBoardTiltCompactVariants : gridBoardTiltVariants).slope as { transform: string[] }).transform.at(-1),
        }}
      ></span>
      {props.children}
    </motion.div>
  )
}

const GridBoard = () => {
  const isSlope = useBettingStore((state) => !state.isBet)
  const isCompact = useConfigStore((state) => state.IS_SMALL_DEVICE)

  return (
    <Wrap>
      <motion.div
        className="rotateBoard"
        animate={isSlope ? 'slope' : 'flat'}
        initial={false}
        variants={isCompact ? gridBoardTiltCompactVariants : gridBoardTiltVariants}
      >
        <BetGrid />
        <Image
          src={bettingGrid}
          className="h-full w-full"
          alt="Betting grid"
        />
      </motion.div>
      <img
        src={mobileGridUpperThick}
        alt="grid upper thick"
        className={cn('boardThick upperThick left-1/2 w-[125.8%] -translate-x-1/2', { 'w-[120.7%]': isCompact })}
      />
      <img
        src={mobileGridLowerThick}
        alt="grid lower thick"
        className={cn('boardThick lowerThick left-1/2 w-[88.3%] -translate-x-[49.5%]', { 'w-[84%]': isCompact })}
      />
    </Wrap>
  )
}
export default GridBoard
