import { ReactNode, useMemo } from 'react'
import DisplacementContainer, { DisplacementContainerProps } from './DisplacementContainer'

interface ChipTrayAnimationProps extends DisplacementContainerProps {
  position: 'left' | 'right' | 'up' | 'down'
  children: ReactNode
  enable?: boolean
}

const ChipTrayAnimation = ({ children, position, enable = true, ...rest }: ChipTrayAnimationProps) => {
  const startPosition = useMemo(() => {
    if (position === 'left') return { x: 16, y: 0 }
    if (position === 'right') return { x: -16, y: 0 }
    if (position === 'up') return { x: 0, y: 16 }
    if (position === 'down') return { x: 0, y: -16 }
    return { x: 0, y: 0 }
  }, [position])

  /*
   * Remove the guard clause to prevent loss of state, re-running of effects, unwanted remounts, and visual disruption.
   * Use motionProps to control the animation and make wrapper persistent.
   */
  // if (!enable) return <>{children}</>

  return (
    <DisplacementContainer
      enable={enable}
      startPosition={startPosition}
      endPosition={{ x: 0, y: 0 }}
      animation={{ duration: 0.6, delay: 1, type: 'spring', stiffness: 250, damping: 60 }}
      {...rest}
    >
      {children}
    </DisplacementContainer>
  )
}

export default ChipTrayAnimation
